const swiper = new Swiper('.achievements-swiper', {
  navigation: {
    nextEl: '.swiper__button_next',
    prevEl: '.swiper__button_prev',
  },
  breakpoints: {
    320: {
      slidesPerView: 1
    },

    576: {
      slidesPerView: 2,
      spaceBetween: 20
    },

    992: {
      slidesPerView: 3,
      spaceBetween: 20
    },

    1200: {
      slidesPerView: 4,
      spaceBetween: 20
    }
  },
  on: {
    slideChangeTransitionEnd: function () {
      if (swiper.isEnd) {
        swiper.navigation.nextEl.forEach(el => el.classList.add('swiper__button_disabled'));
        document.querySelector('.achievements-swiper').classList.add('is-last');
      } else {
        swiper.navigation.nextEl.forEach(el => el.classList.remove('swiper__button_disabled'));
      }

      if (swiper.realIndex !== 0 || swiper.isEnd) {
        swiper.navigation.prevEl.forEach(el => el.classList.remove('swiper__button_disabled'));
      } else if (swiper.realIndex == 0) {
        swiper.navigation.prevEl.forEach(el => el.classList.add('swiper__button_disabled'));
        document.querySelector('.achievements-swiper').classList.remove('is-last');
      }
    },
  }
});

if (document.querySelector('.object-gallery') || document.querySelector('.project-page-about__gallery')) {
  const swiperObject = new Swiper('.object-gallery-swiper', {
    navigation: {
      nextEl: '.swiper__button_next',
      prevEl: '.swiper__button_prev',
    },
    breakpoints: {
      320: {
        slidesPerView: 1
      },

      992: {
        slidesPerView: 2,
        spaceBetween: 20
      },

      1200: {
        slidesPerView: 4,
        spaceBetween: 40
      }
    },
    on: {
      slideChangeTransitionEnd: function () {
        if (swiperObject.realIndex == 1) {
          swiperObject.navigation.prevEl.forEach(el => el.classList.remove('swiper__button_disabled'))
        } else if (swiperObject.realIndex == 0) {
          swiperObject.navigation.prevEl.forEach(el => el.classList.add('swiper__button_disabled'))
        }
      },
    }
  });
}

window.addEventListener('DOMContentLoaded', () => {
  const openNavbar = () => {
    const burger = document.querySelector('.header-burger');
    const navbar = document.querySelector('.navbar');
    const overlay = document.querySelector('.overlay')

    burger.addEventListener('click', () => {
      navbar.classList.toggle('is-active');
      document.querySelector('main').classList.add('overflow-hidden');
      document.body.classList.add('overflow-hidden');

      if (navbar.classList.contains('is-active')) {
        overlay.classList.add('is-active');
      }
    })

    navbar.querySelector('.navbar-mobile__close').addEventListener('click', () => {
      navbar.classList.remove('is-active');
      overlay.classList.remove('is-active');
      document.querySelector('main').classList.remove('overflow-hidden');
      document.body.classList.remove('overflow-hidden');
    })

    overlay.addEventListener('click', () => {
      navbar.classList.remove('is-active');
      overlay.classList.remove('is-active');
      document.querySelector('main').classList.remove('overflow-hidden');
      document.body.classList.remove('overflow-hidden');
    })
  }
  openNavbar();

  if (document.querySelector('.objects')) {
    const tabs = () => {
      const tabsBtns = document.querySelector('.objects-tabs');
      const objectsTabs = document.querySelector('.objects-content');

      const getActiveTabName = () => {
        return tabsBtns.querySelector('.objects-tabs__btn_active').dataset.status;
      }

      const setActiveContent = () => {
        if (objectsTabs.querySelector('.objects-list_active')) {
          objectsTabs.querySelector('.objects-list_active').classList.remove('objects-list_active');
        }
        objectsTabs.querySelector(`[data-status=${getActiveTabName()}]`).classList.add('objects-list_active');
      }

      if (!tabsBtns.querySelector('.objects-tabs__btn_active')) {
        tabsBtns.querySelector('.objects-tabs__btn').classList.add('objects-tabs__btn_active');
      }

      setActiveContent(getActiveTabName());

      tabsBtns.addEventListener('click', e => {
        const btn = e.target.closest('.objects-tabs__btn');
        if (!btn) return;
        if (btn.classList.contains('objects-tabs__btn_active')) return;

        if (tabsBtns.querySelector('.objects-tabs__btn_active')) {
          tabsBtns.querySelector('.objects-tabs__btn_active').classList.remove('objects-tabs__btn_active');
        }

        btn.classList.add('objects-tabs__btn_active');

        setActiveContent(getActiveTabName());
      })

      const setCountObjectsBtn = () => {
        const objectsBtns = document.querySelectorAll('.objects-tabs__btn');
        const objectsLists = document.querySelectorAll('.objects-list');

        for (let i = 0; i < objectsBtns.length; i++) {
          objectsBtns[i].querySelector('.objects-tabs__btn_count').innerText = objectsLists[i].children.length;
        }
      }
      setCountObjectsBtn();
    }
    tabs();
  }

  if (document.querySelector('.project-page-about')) {
    const tabsInProjectPage = () => {
      const tabsBtns = document.querySelector('.project-page-about__buttons');
      const objectsTabs = document.querySelector('.project-page-about__content');

      const getActiveTabName = () => {
        return tabsBtns.querySelector('.project-page-about__btn_active').dataset.info;
      }

      const setActiveContent = () => {
        if (objectsTabs.querySelector('.project-page-about__item_active')) {
          objectsTabs.querySelector('.project-page-about__item_active').classList.remove('project-page-about__item_active');
        }
        objectsTabs.querySelector(`[data-info=${getActiveTabName()}]`).classList.add('project-page-about__item_active');
      }

      if (!tabsBtns.querySelector('.project-page-about__btn_active')) {
        tabsBtns.querySelector('.project-page-about__btn').classList.add('project-page-about__btn_active');
      }

      setActiveContent(getActiveTabName());

      tabsBtns.addEventListener('click', e => {
        const btn = e.target.closest('.project-page-about__btn');
        if (!btn) return;
        if (btn.classList.contains('project-page-about__btn_active')) return;

        if (tabsBtns.querySelector('.project-page-about__btn_active')) {
          tabsBtns.querySelector('.project-page-about__btn_active').classList.remove('project-page-about__btn_active');
        }

        btn.classList.add('project-page-about__btn_active');

        setActiveContent(getActiveTabName());
      })
    }
    tabsInProjectPage();
  }

  if (document.querySelector('.stories-main')) {
    const storiesPagination = () => {
      const content = document.querySelector('.stories-main-list');
      let itemsPerPage = 9;
      let currentPage = 0;
      const items = Array.from(content.querySelectorAll('.stories-main-item'));

      const showPage = page => {
        const startIndex = page * itemsPerPage;
        const endIndex = startIndex + itemsPerPage;
        items.forEach((item, index) => {
          item.classList.toggle('hidden', index < startIndex || index >= endIndex);
        })
        updateActiveButtonStates();
      }

      const createPageButton = () => {
        const totalPages = Math.ceil(items.length / itemsPerPage);
        const paginationContainer = document.querySelector('.stories-main-pagination');

        for (let i = 0; i < totalPages; i++) {
          const pageButton = document.createElement('button');
          pageButton.classList.add('btn-reset');
          pageButton.classList.add('stories-main-pagination__btn');
          pageButton.textContent = i + 1;
          pageButton.addEventListener('click', () => {
            currentPage = i;
            showPage(currentPage);
            updateActiveButtonStates();
          })
          paginationContainer.appendChild(pageButton);
        }

        const createPageButtonNext = () => {
          const pageButtonNext = document.createElement('button');
          pageButtonNext.textContent = '>';
          pageButtonNext.classList.add('btn-reset');
          pageButtonNext.classList.add('stories-main-pagination__btn');
          paginationContainer.appendChild(pageButtonNext);

          pageButtonNext.addEventListener('click', () => {
            currentPage++;
            showPage(currentPage);
            updateActiveButtonStates();
          })
        }
        createPageButtonNext();

        const btnShowMore = document.querySelector('.stories-main__show-more');
        btnShowMore.addEventListener('click', () => {
          itemsPerPage += 9;
          showPage(currentPage);
          updateActiveButtonStates();
        })
      }

      const updateActiveButtonStates = () => {
        const pageButtons = document.querySelectorAll('.stories-main-pagination__btn');
        pageButtons.forEach((button, index) => {
          if (index === currentPage) {
            button.classList.add('is-active');
          } else {
            button.classList.remove('is-active');
          }
        })
      }

      createPageButton();
      showPage(currentPage);
    }
    storiesPagination();
  }

  if (document.querySelector('.project-activities-contribution')) {
    const animationNumbers = () => {
      const options = {
        startVal: 0,
        duration: 6,
        separator: ' ',
        suffix: ' ₽',
      };
      const animateCount = new countUp.CountUp('countUp', 15000000, options);
      animateCount.start();
    }
    animationNumbers();
  }

  if (document.querySelector('.activities-projects-content')) {
    const tabs = () => {
      const tabsBtns = document.querySelector('.activities-projects-buttons');
      const objectsTabs = document.querySelector('.activities-projects-content');

      const getActiveTabName = () => {
        return tabsBtns.querySelector('.activities-projects-buttons__btn_active').dataset.status;
      }

      const setActiveContent = () => {
        if (objectsTabs.querySelector('.activities-projects-list_active')) {
          objectsTabs.querySelector('.activities-projects-list_active').classList.remove('activities-projects-list_active');
        }
        objectsTabs.querySelector(`[data-status=${getActiveTabName()}]`).classList.add('activities-projects-list_active');
      }

      if (!tabsBtns.querySelector('.activities-projects-buttons__btn_active')) {
        tabsBtns.querySelector('.activities-projects-buttons__btn').classList.add('activities-projects-buttons__btn_active');
      }

      setActiveContent(getActiveTabName());

      tabsBtns.addEventListener('click', e => {
        const btn = e.target.closest('.activities-projects-buttons__btn');
        if (!btn) return;
        if (btn.classList.contains('activities-projects-buttons__btn_active')) return;

        if (tabsBtns.querySelector('.activities-projects-buttons__btn_active')) {
          tabsBtns.querySelector('.activities-projects-buttons__btn_active').classList.remove('activities-projects-buttons__btn_active');
        }

        btn.classList.add('activities-projects-buttons__btn_active');

        setActiveContent(getActiveTabName());
      })

      const setCountObjectsBtn = () => {
        const objectsBtns = document.querySelectorAll('.activities-projects-buttons__btn');
        const objectsLists = document.querySelectorAll('.activities-projects-list');

        for (let i = 0; i < objectsBtns.length; i++) {
          objectsBtns[i].querySelector('.activities-projects-buttons__btn_count').innerText = objectsLists[i].children.length;
        }
      }
      setCountObjectsBtn();
    }
    tabs();
  }

  if (document.querySelector('.project-page-about')) {
    const animationNumbers = () => {
      const options = {
        startVal: 0,
        duration: 6,
        separator: ' ',
        suffix: ' ₽',
      };
      const animateCount = new countUp.CountUp('projectPageCountUp', 650000, options);
      animateCount.start();
    }
    animationNumbers();
  }
})

new WOW().init();